import React from 'react';
import about_back from '../assets/about_back.jpg';
import '../demo.css';

const About =()=>{
	return(
		<section id='About' className='About yellow'style={{width:'100vw',height:'100vh'}}>

			<div className='our-story-card'>

				<div className='about_back our-story-card-background'>
					<div className="background image-wrapper"  >
						<img src={about_back} alt='bg' className='bg-img bg'/>
						<div className='image-wrapper-gradient-about'></div>
					</div>
				</div>


				<div className='card-text-about pb6'>
					<span><h1 className='f1'>About</h1></span>
					<div className='v1'> </div>
				</div>


				<div className='about '>
					<span><h3>Welcome to our second team project....<span className='yellow'>Image-Chronicle</span> .</h3></span>
					
						<span className='pa2'>
							<h5>
									We are a creative studio that loves to learn, collaborate and create new stuffs. 
									We are a group of three individuals with a passion for creativity - creativity makes us happy.
									We're excited to start a visual dialogue, learn about you, and make something beautiful together.
							</h5>
						</span>
					
					
						<span>
							<h5>
									We hope you enjoy our projects/models as much as we enjoy offering them to you.
									 If you have any questions or comments, please don't hesitate to contact us.
							</h5>
						</span>
						<span className='salutation f3 yellow pa4' style={{float:'left'}}>
								Sincerely,<br/>
								The Team
						</span>
					
				</div>
			</div>
		</section >
		);
}

export default About;