import React from 'react';
import '../index.css';
import upload from '../assets/upload.png';
import one from '../assets/1.jpg';
import two from '../assets/2.jpg';
import three from '../assets/3.jpg';
import four from '../assets/4.jpg';
import five from '../assets/5.jpg';
import six from '../assets/6.jpg';
import seven from '../assets/7.jpg';
import eight from '../assets/8.jpg';
import {Button,Form} from 'react-bootstrap';
import axios from 'axios';
import CircleLoader from "react-spinners/CircleLoader";
import {toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

toast.configure()

let images =[seven,one,two,three,upload,four,five,six,eight];


class ImageSelectors extends React.Component {

	constructor(props){
		super(props);

		this.state={
			selectedItem : -1,
			image: null,
			dummy: null,
			imageURL:'',
			isLoading:false,
			caption: '',
			frame: 0,
		};

		this.handleUploadImage = this.handleUploadImage.bind(this);
		this.toggleFrame = this.toggleFrame.bind(this)
	}

	changeClass = (select,event) => {
		//console.log(select)
		let flag=false
		if (this.state.selectedItem!==-1)
		{
			(document.getElementById(this.state.selectedItem).classList.remove("SelectorBox"));
			 //document.getElementById('text').textContent = 'Use this photo';
			 document.getElementById(select).classList.add("SelectorBox");
			 document.getElementById('SelectorTag').style.display = 'block';
			 if (flag===false)
			 {
			 	document.getElementById(this.state.selectedItem).classList.remove("input-group");
			 	document.getElementById('Browse').style.display = 'none';
			 }
			 flag = true;
		}
		if (select===4){
			
			//document.getElementById('uploadText').textContent = 'Browse a photo';
			//console.log(flag);
			if (flag===true){
				document.getElementById(select).classList.remove("SelectorBox" );
				document.getElementById('SelectorTag').style.display = 'none';
				flag=false;
			}

			document.getElementById('Browse').style.display = 'flex';
			document.getElementById(select).classList.add("SelectorBox");
			document.getElementById('SelectorTag').style.display = 'none';
		
			//console.log(document.getElementById('uploadText').textContent);
		}

		if ((this.state.selectedItem===-1)&&(select!==4)){
			document.getElementById(select).classList.add("SelectorBox");
			document.getElementById('SelectorTag').style.display = 'block';
		}
		
		this.setState({selectedItem:select});
		if (select !== 4)
		{
			console.log(select+" "+this.state.selectedItem)
			this.setState({dummy: images[select]})
		}
	}

	

	removeClass = (event)=>{
		document.getElementById('SelectorTag').style.display = 'none';
		document.getElementById('Browse').style.display='none';
		document.getElementById(this.state.selectedItem).classList.remove("SelectorBox");
		this.setState({selectedItem:-1});
	}


	onChange = (event) =>{
		this.setState({
			image: event.target.files[0]
		});
		//console.log("Changed"+this.state.image);

		let reader = new FileReader();

		reader.onloadend = () => {
			this.setState({
				imageURL: reader.result
			});
		}

		reader.readAsDataURL(event.target.files[0])
	}


	handleUploadImage=(event)=>{
		event.preventDefault();

		
		
		if (this.state.image === null){
			toast.warn('Please choose an image!!!')
			return;
		}

		else{

			let file = this.state.image;
			console.log(this.state.image);
			this.setState({isLoading:true,frame:1});

			let data = new FormData();
			//data.append('image',file);
			data.append('photo', file);
			//console.log(data);

			const formData = new FormData()
			  formData.append(
			    'photo',
			    this.state.image,
			    
			  )
			  axios.post('/prediction', formData)
			  .then((json) => {
			  	this.setState({
			  		//imageURL: `http://localhost:5000/${this.state.image}`,
			  		isLoading:false,
			  		caption: json,
			  		image: null,
			  	})
			  })
			  .catch((error) => console.log(error));
		}

	}

	handleServeLocal = (event) => {

		event.preventDefault();

		this.setState({isLoading:true,frame:1});
		let localFile = this.state.dummy;
		//console.log(localFile)

		const formData ={"path":localFile};
		  axios.post('/prediction1', formData).then((json) => {
		  	this.setState({
		  		imageURL: `http://localhost:5000${this.state.dummy}`,
		  		isLoading:false,
		  		caption: json,
		  		image:null,
		  	})
		  })
		  .catch((error) => console.log(error));


	}

	toggleFrame=()=>{
		//e.preventDefault()
		this.setState({
			frame: 0,
		});
	}
	
	handleAudio=()=>{
		//list of languages is not probably loaded please wait to load
		if(window.speechSynthesis.getVoices().length == 0) {
			window.speechSynthesis.addEventListener('voiceschanged', function() {
				this.textToSpeech();
			});
		}
		else {
			// languages list available, no need to wait
			this.textToSpeech();
		}
	}

	textToSpeech = ()=>{
		// get all voices that browser offers
		var available_voices = window.speechSynthesis.getVoices();

		// this will hold an english voice
		var english_voice = '';

		// find voice by language locale "en-US"
		// if not then select the first voice
		for(var i=0; i<available_voices.length; i++) {
			if(available_voices[i].lang === 'en-US') {
				english_voice = available_voices[i];
				break;
			}
		}
		if(english_voice === '')
			english_voice = available_voices[0];

		// new SpeechSynthesisUtterance object
		var utter = new SpeechSynthesisUtterance();
		utter.rate = 1;
		utter.pitch = 1;
		utter.text = this.state.caption.data;
		utter.voice = english_voice;

		// event after text has been spoken
		utter.onend = function() {
			console.log('Speech has finished');
		}

		// speak
		window.speechSynthesis.speak(utter);
	}

	render(){
		
		let {image,frame,isLoading,caption,imageURL} = this.state;

		if (frame)
		{
			return (
				<div className='tc ' id='outputWindow' style={{top:'50%',left:'50%'}}>
				
			        	{	
			        		(isLoading) ?
			        			<div className="yellow">
			        				<div className='sweet-loading container' style={{justifyContent:'center',display:'flex'}}>
			        					<CircleLoader
			        						size={150}
			        						color={'gold'}
			        						loading={isLoading}
			        						/>
			        				</div>
			        				<h1 className='tc ' style={{justifyContent:'center',display:'flex'}}>
			        					Analyzing...
			        				</h1>
			        			</div>
			        			:
			        			<div>
			        			<img src={imageURL} alt='img' style={{width:'500px',height:'auto',border:'20px wheat solid'}}/>
			        			<br/> 
			        			<span className="tc f4">
					        		<span className='yellow'>
					        			The predicted Caption is :
					        		</span>  <br/>
					        		<span className='white pa2'>{caption.data}</span>
					        		<Button variant='outline-info' className='btn-default btn' type='button' onClick={this.handleAudio}>
					        			Play Caption
					        		</Button>
					        	</span> 
					        	<div className='pa4'>
					        		<Button variant='outline-primary' id="samplePhotoSelect" type="button" value="submit" className="btn btn-default " 
					        		onClick={this.toggleFrame}>
							                <span className="button" style={{'marginRight': '4px' }}></span>
							                <span id='uploadText' className="text">Try Another</span>
							          </Button>
					        	</div>
					        	</div>
			        		
			        	}

				</div>
				);
		}
		else
		{
			return (
				<div id='inputForm'>
				<form noValidate>
					<div className='outer tc mt4 mb4' style={{display:'block'}}>
						<div className="scrolling-wrapper" id='imageDiv'>
							<div className='scrolly pt4 pr4 pb4 mb4	 ml4 mr4'>
							{
								images.map((name,i) =>{
									return (
										<div className='card ' key={i}>
											<img
											className='name'
											id={i}
											key={i}
											src={name}
											alt={name}
											name='photo'
											onClick = {(this.state.selectedItem===i)?(e) => this.removeClass(e) : (e) => this.changeClass(i,e)}
											style={{width:'250px',height:'250px'}}
											/>
										</div>
										);
								})
							}
							</div>	
						</div>

						
							<div className='SelectorTag' id="SelectorTag" style={{display:'none'}}>
					            <Button variant='outline-primary' id="samplePhotoSelect" type="submit" value="submit" className="btn btn-default " onClick={this.handleServeLocal}>
					                <span className="button" style={{'marginRight': '4px' }}></span>
					                <span id='uploadText' className="text">Upload from here</span>
					            </Button>
				        	</div>
				        	<div className="SelectorTag input-group" id="Browse" style={{display:'none'}}>
							  <div className="input-group-prepend">
							    <span className="input-group-text" id="inputGroupFileAddon01">
							      Upload
							    </span>
							  </div>
							  <div className="custom-file">
							    <input
							      type="file"
							      className="custom-file-input"
							      id="inputGroupFile01"
							      aria-describedby="inputGroupFileAddon01"
							      accept='image/png , image/jpeg'
							      name='file'
							      onChange={this.onChange}
							      ref={(ref) => { this.uploadInput = ref; }}
							      required
							    />
							    <label className="custom-file-label" htmlFor="inputGroupFile01">
							      Choose file
							    </label>
							  </div>
							  <div style={{paddingLeft:'2rem'}}>
							  	<Button variant='outline-primary' disabled={(image===null)? true : false} type="submit" value="submit" className='btn btn-default' onClick={this.handleUploadImage} >
							  		<span className='pa2'>Submit</span>
							  	</Button>
							  </div><br/>
							</div>
							<div className='tc'>
							  	<br/>
							  	{image!==null?<span className='white'>{image.name}</span>:<div></div>}
							  </div>
					</div>
				</form>
				</div>
				);
		}
	}
}

export default ImageSelectors;