import React from 'react';
import {Navbar,Nav,Button,Form,FormControl} from 'react-bootstrap';

const Navigation=()=>{
	return (
		<Navbar  bg="black" variant='dark' expand="lg" className='pa3 ' style={{background:'transparent',backgroundImage: 'linear-gradient(180deg,rgba(0,0,0,0.00) 0%,rgba(0,0,0,0.00) 100%)',boxShadow:'0px 0px'}}>
		  <div className='tc pa2'>
			  <Navbar.Brand href="#Our_project" className='pr5'>
			  	Image-Chronicle
			  </Navbar.Brand>
		  </div>
		  <Navbar.Toggle aria-controls="basic-navbar-nav" />
		  <Navbar.Collapse id="basic-navbar-nav" className='tc'>
		  <div className='tc '>
		    <Nav className="mr-auto tc">
		      <Nav.Link href="#Home" className='pr2'>Home</Nav.Link>
		      <Nav.Link href="#project_description">Project Description</Nav.Link>
		      <Nav.Link href='#Our_project'>Our Project</Nav.Link>
		      <Nav.Link href='#Team'>Team</Nav.Link>
		      <Nav.Link href='#About'>About us</Nav.Link>
		      <Nav.Link href='#Contact'>Contact us</Nav.Link>
		    </Nav>
		    </div>
		    {/*<Form inline>
		      <FormControl type="text" placeholder="Search" className="mr-sm-2" />
		      <Button className='button' variant="outline-success">Search</Button>
		    </Form>*/}

		  </Navbar.Collapse>

		</Navbar>
		);
}
export default Navigation;