import React from 'react';
import Project_description from './Project_description';
import back from '../assets/back.jpg';
import {Button} from 'react-bootstrap';
import '../demo.css';

const Home =()=>{
	return(
		<section id='Home' className='Home our-story-card hero-card' style={{height:'100vh'}}>


			<div className='Home_back our-story-card-background'>
				<div className='image-wrapper'>
					<img src={back} alt='bg' className='bg-img bg'/>
					<div className='image-wrapper-gradient'></div>
				</div>
			</div>


			<div className='card-text_home tc'>
				<h1 className='white' style={{float:'left'}}>Your image </h1>
				<h1 className='yellow' style={{float:'right'}}>can talk</h1>
				<hr style={{clear:'both'}}/>
				<h3 className='white' >Want to see your image speak</h3>
				<div>
					<div className='button_wrap pa4 '>
						<Button href = '#project_description' variant ='outline-warning' type='button' className='btn btn-default'>
							<span id='text' className="text">Understand the project</span>
						</Button>
					</div>
				</div>
				<h3 className='yellow'>We will try not to disappoint you</h3>
				<hr style={{clear:'both'}}/>
				
			</div>
			
		
		</section>
		);
}

export default Home;