import React from 'react';
import {Button} from 'react-bootstrap';
import dark_back from '../assets/dark_back.jpeg';
import '../demo.css';
import axios from 'axios';
import {toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

toast.configure()


class Contact extends React.Component {

	constructor(props){
		super(props);
		this.state={
			name:'',
			email:'',
			phone:'',
			message:'',
			//alert_message:'',
			isEnabled: true
		}
	}

	resetForm(){
		this.setState({
			name:'',
			email:'',
			phone:'',
			message:''
		})
	}

	onNameChange = (event)=>{
		this.setState({
			name: event.target.value
		})
	}

	onEmailChange =(event)=>{
		console.log(event.target.value)
		this.setState({
			email: event.target.value
		})
	}

	onPhoneChange = (event)=>{
		this.setState({
			phone: event.target.value
		})
	}
	onMessageChange = (event)=>{
		this.setState({
			message: event.target.value
		})
	}


	 onSubmit =(e)=>{
	 	
	 	e.preventDefault();
	 	this.setState({
	 		isEnabled: false
	 	})
		console.log(this.state)
		axios.post('/SubmitDetails',this.state)
		.then((response)=>{
			if (response.data.status ==='success'){
				toast.success('Thank You. Your message has been sent.',{ position:toast.POSITION.BOTTOM_RIGHT })
				this.setState({isEnabled:true})
				this.resetForm();
			}
			else if (response.data.status === 'failure'){
				this.setState({isEnabled:true})
				toast.warn('Network should be connected.',{position:toast.POSITION.BOTTOM_RIGHT})
			}
		}).catch(error=>{
			this.setState({isEnabled:true})
			toast.warn('Some error occured while contacting.',{ position:toast.POSITION.BOTTOM_RIGHT })
		})

	}


	render(){
	return(
		<section id='Contact' className='contact_container'style={{width:'100vw',height:'100vh'}}>

			<div className='our-story-card pb4' style={{width:'100vw',height:'100vh'}}>
			
				<div className='contact_back our-story-card-background'>
					<div className="background image-wrapper"  >
						<img src={dark_back} alt='bg' className='bg-img bg' />
						<div className='image-wrapper-gradient'></div>
					</div>
				</div>

				<div className='card-text-contact'>
					<div id="1">
							<div className='text_contact ttu tc'>
								<div className='title-h1'>
									<span className='text_title f1'style={{color:'white'}}>
										contact us
										<br/>
									</span>
								</div>
							</div>
							<div className='text_contact ttu tc'>
								<div className='title-h4'>
									<span className='text_t f3'style={{color:'white'}}>
										and we will help you to make your gem
										<br/>
									</span>
								</div>
							</div>
					</div>
					<div id="2">
						<div className='box tc' >
							<div className='form tc'>
								<form method="post" className="wpcf7-form" noValidate>
									
									<div className='form-sidebar ma4 pa4' >
										<p className='mb8'>
											<span className='name_input w-100' >
												<input type="name" 
													name="name" 
													size="40" 
													onChange= {this.onNameChange}
													value ={this.state.name}
													placeholder="Name*" className="ba b--white-50 bw1 br2"
													style={{width:'50%', height:'30px',backgroundColor:'rgba(230,230,230,0)',color:'white'}} required/>
											</span>
										</p>
										<p className='mb8'>
											<span className='mail_input w-100 ' >
												<input type="email" 
												onChange={this.onEmailChange}
												value = {this.state.email}
												name="email" pattern="^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$"  
												size="40" className="wpcf7-form-control wpcf7-text wpcf7-validates-as-required ba b--white-50 bw1 br2" 
												placeholder="Email*" style={{width:'50%', height:'30px',backgroundColor:'rgba(230,230,230,0)',color:'white'}} required/>
											</span>
										</p>
										<p className='mb8'>
											<span className='mail_input w-100' >
												<input 
												name="phone" minLength='10' maxLength='10'
												value={this.state.phone}
												onChange = {this.onPhoneChange}
												size="40" className="wpcf7-form-control wpcf7-text wpcf7-validates-as-required ba b--white-50 bw1 br2" 
												placeholder="Your Phone Number*"
												type='phone' style={{width:'50%', height:'30px',backgroundColor:'rgba(230,230,230,0)',color:'white'}} required/>
											</span>
										</p>
										<p className='mb8'>
											<span className=' w-100' >
												<textarea placeholder='Your Message' 
												maxLength='300' minLength='10' type='text' 
												className='msgbox ba b--white-50 bw1 br2' 
												value={this.state.message}
												onChange={this.onMessageChange}
												style={{width:'50%', height:'150px',textAlign:'left',backgroundColor:'rgba(230,230,230,0)',color:'white'}} required>
												</textarea>
											</span>
										</p>
										<Button type='submit' disabled={!this.state.isEnabled} onClick={this.onSubmit} value='Submit Message' style={{background:'blue',borderRadius:'8px',color:'white',height:'2rem'}}>
											Submit
										</Button>
									</div>
								</form>
							</div>
						</div>
					</div>
				  </div>
				</div>
		</section>
		);
	}

	
}

export default Contact;