import React from 'react';
import {NavLink,Route,Switch,BrowserRouter as Router} from 'react-router-dom';
import Home from './Components/Home';
import NotFound from './Components/NotFound';
import About from './Components/About';
import Our_project from './Components/Our_project';
import Project_description from './Components/Project_description';
import Contact from './Components/Contact';
import Team from './Components/Team';

class MainPage extends React.Component{
	render()
	{
		return(
				<div className='container  pl2 pr2 ' style={{width:'100vw'}}>
				
				<Router>
					<div>
						<div className='tc bg-black'style={{width:'100%' ,height:'70px',top:'1px',left:'0',position:'absolute'}}>
							<nav className='tc pt3'>
								<NavLink exact to='/' className='home link pa2 ma2'> Home</NavLink>
								<NavLink to='/project_description' className='projectD link pa2 ma2'>Project Description</NavLink>
								<NavLink to='/our_project' className='project link pa2 ma2'> Our Project</NavLink> 
								<NavLink to='/team' className='team link pa2 ma2'> Team</NavLink>
								<NavLink to='/about' className='about link pa2 ma2'> About us</NavLink>
								<NavLink to='/contact' className='contact link pa2 ma2'>Contact Us</NavLink>
							</nav>
						</div>
					</div>
					<Switch>
						<Route 
							path='/'
							component = {Home}
							exact
						/>
						<Route
							path='/project_description'
							component={Project_description}
						/>
						<Route
							path='/our_project'
							component={Our_project}
						/>
						<Route
							path='/team'
							component={Team}
						/>
						<Route
							path='/about'
							component={About}
						/>
						<Route
							path='/contact'
							component ={Contact}
						/>
						<Route component={NotFound}/>
					</Switch>
					</Router>
				</div>
			);
	}
}

export default MainPage;